html {
  scroll-behavior: smooth;
  scroll-padding-top: 5em;
}

body {
  background-color: #0e0f0f;
  color: #FFFFFF;
}

@media screen and (max-width: 1024px) {
  html {
    scroll-padding-top: 8rem;
  }
}
footer {
  background-color: #141515;
}
footer * {
  color: #3fb950;
  border-color: #3fb950 !important;
}

.main-container {
  max-width: 1440px;
  margin: 0 auto 0 auto;
  display: grid;
  grid-template-columns: minmax(0, 1fr) minmax(0, 2.5fr) minmax(0, 15rem);
}
.main-container a {
  color: unset;
  text-decoration: none;
}
.main-container a[href]:hover {
  text-decoration: underline;
}
.main-container .sidebar {
  max-height: calc(100vh - 4rem);
  padding-top: 2rem;
  padding-right: 2rem;
  position: sticky;
  top: 4rem;
  z-index: 1;
  transform: 0;
}
.main-container .sidebar strong {
  color: #3fb950;
  font-size: 1.25em;
}
.main-container .sidebar ul {
  list-style: none;
}
.main-container .sidebar ul .active {
  background-color: #141515;
  border-radius: 1rem;
  border: solid #3fb950;
}
.main-container .sidebar ul li {
  padding: 1rem 0.25rem 1rem 1rem;
}
.main-container .sidebar ul li summary {
  margin-bottom: 1rem;
}
.main-container .sidebar ul li details summary {
  margin-bottom: 1rem;
}
.main-container .sidebar ul li details ul {
  padding-left: 1rem;
}
.main-container .sidebar .open-button {
  position: absolute;
  left: 20%;
  background-color: #141515;
  width: fit-content;
  height: 3rem;
  margin-top: -1px;
  border-left: solid #3fb950;
  border-bottom: solid #3fb950;
  border-right: solid #3fb950;
  border-bottom-left-radius: 0.75rem;
  border-bottom-right-radius: 0.75rem;
  display: none;
  flex-direction: column;
  justify-content: center;
  transition: background-color 250ms;
}
.main-container .sidebar .open-button[class="open-button open"] {
  background-color: #0e0f0f;
}
.main-container .sidebar .open-button input {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
}
.main-container .sidebar .open-button input:checked + label {
  color: #3fb950;
}
.main-container .sidebar .open-button input:checked + label .icons {
  transform: translateY(2px);
  rotate: 180deg;
}
.main-container .sidebar .open-button label {
  color: #FFFFFF;
  padding: 0 1rem 0 0.75rem;
}
.main-container .sidebar .open-button label .icons {
  rotate: 0deg;
  transition: background-color 250ms rotate 250ms;
}
.main-container main {
  background-color: #121212;
  z-index: 0;
  padding: 3em 3em 3em 3em;
  font-size: 1.1rem;
  line-height: 1.5;
}
.main-container main iframe {
  aspect-ratio: 16/9;
  width: 100%;
  height: auto;
  margin-top: 1em;
  border-radius: 1em;
  border: solid #3fb950;
}
.main-container main article {
  display: block;
}
.main-container main article h1 {
  font-size: 2.5em;
  margin-bottom: 1em;
  font-family: satoshi-black, arial, sans-serif;
}
.main-container main article .module-description {
  margin-bottom: 3em;
}
.main-container main article a {
  color: #3fb950;
  font-family: satoshi-black, arial, sans-serif;
}
.main-container main article h2 {
  font-size: 2em;
  font-family: satoshi-black, arial, sans-serif;
}
.main-container main article h3 {
  margin-top: 1em;
  margin-bottom: 0.5em;
  font-size: 1.5em;
  font-family: satoshi-black, arial, sans-serif;
}
.main-container main article .highlight {
  color: #3fb950;
}
.main-container main article .spoiler-container {
  position: relative;
}
.main-container main article .spoiler-container .spoiler {
  background-color: #141515;
  border: solid #3fb950;
  border-radius: 1rem;
  width: 105%;
  height: 110%;
  position: absolute;
  top: 0;
  bottom: 0;
  transform: translate(-2.5%, -5%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  transition: opacity 500ms;
}
@supports (backdrop-filter: blur()) {
  .main-container main article .spoiler-container .spoiler {
    background-color: rgba(29, 29, 33, 0.4);
    backdrop-filter: blur(1rem);
  }
}
.main-container main article .fancy-button {
  background-color: transparent !important;
  border: solid #3fb950;
  border-radius: 4rem;
  padding: 0.5em 0.75em 0.5em 0.75em;
  font-family: satoshi-bold, arial, sans-serif;
  font-size: 1.25em;
  text-decoration: none !important;
  border-color: #3fb950;
  color: #3fb950;
  transition: opacity 250ms;
}
.main-container main article .fancy-button .flair {
  background-color: #3fb950;
}
.main-container main article .fancy-button:hover {
  color: #FFFFFF;
}
.main-container main article section {
  margin-top: 1em;
  margin-bottom: 2em;
}
.main-container main article section ol {
  padding-left: 1em;
}
.main-container main article section .solving div {
  display: flex;
  flex-direction: column;
  padding-left: 1em;
}
.main-container main article section .solving .indent {
  padding-left: 1em;
  margin-bottom: 1em;
}
.main-container main article section .callout {
  background-color: #191a1f;
  padding: 1.5em 1.5em 1.5em 1.5em;
  border-radius: 1rem;
  border: solid #3fb950;
}
.main-container main article section .callout h3 {
  font-size: 1.25em;
  font-family: satoshi-black, arial, sans-serif;
  margin-top: 0;
  margin-bottom: 0;
}
.main-container main article #flashcards {
  margin-top: 4em;
}
.main-container main article #flashcards .fancy-button {
  font-family: satoshi-bold, arial, sans-serif;
  margin-top: 2em;
}
.main-container .toc {
  max-height: calc(100vh - 4rem);
  position: sticky;
  top: 4rem;
}
@media screen and (max-width: 1440px) {
  .main-container {
    grid-template-columns: minmax(0, 1fr) minmax(0, 2.5fr);
  }
  .main-container .toc {
    display: none;
  }
}
@media screen and (max-width: 1024px) {
  .main-container {
    grid-template-columns: 1fr;
  }
  .main-container .sidebar {
    background-color: #0e0f0f;
    width: 100%;
    border-bottom: solid #3fb950;
    margin-top: 2px;
    position: fixed;
    transform: translateY(-100%);
  }
  .main-container .sidebar > ul {
    height: 50vh;
    overflow-y: scroll;
  }
  .main-container .sidebar .open-button {
    display: flex;
  }
  .main-container main {
    font-size: 1.15rem;
    padding-top: 6em;
  }
}
@media screen and (max-width: 768px) {
  .main-container .sidebar .open-button {
    left: 1rem;
  }
  .main-container main {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    font-size: 1.25rem;
  }
  .main-container main .desmos {
    aspect-ratio: 1;
    height: auto;
  }
}